import React, { useState, useCallback, useEffect } from 'react'
import styled from 'styled-components'
import { Card, CardBody, Heading, Text, Modal, Button, CopyToClipboard } from '@pancakeswap-libs/uikit'
// import Network from './components/Network';
import Divider from 'views/Farms/components/Divider'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import CardValue2 from 'views/Home/components/CardValue2'
import { useReferralContract } from 'hooks/useContract'
import UnlockButton from 'components/UnlockButton'
import { getBalanceNumber } from '../../utils/formatBalance'

const StyledReferal = styled(Card)`
  // margin-right: auto;
  padding: 30px;
  max-width: 65rem;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 5rem;
`
const Row = styled.div`
  display: flex;
  margin-top: 0.5rem;
  align-items: center;
`
const Div = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
`

const Actions = styled.div`
  margin-top: 10px;
`

const Padding = styled.div`
  padding: 1rem;
  text-align: center;
`

const Referal = () => {
  const [refEarnings, setRefEarnings] = useState(0)
  const [refCount, setRefCount] = useState(0)
  const [myRef, setMyRef] = useState(false)

  const refContract = useReferralContract()
  const { account } = useWallet()

  useEffect(() => {
    const loadData = async () => {
      const _refEarnings = await refContract.methods.totalReferralCommissions(account).call()
      const _refCount = await refContract.methods.referralsCount(account).call()
      const _myRef = await refContract.methods.getReferrer(account).call()

      setRefEarnings(getBalanceNumber(_refEarnings))
      setRefCount(_refCount)
      setMyRef(_myRef)
    }

    if (refContract && account) {
      loadData()
    }
  }, [refContract, account])

  function copyToClipboard(text) {
    const input = document.createElement('input')
    input.setAttribute('value', text)
    document.body.appendChild(input)
    input.select()
    const result = document.execCommand('copy')
    document.body.removeChild(input)
    return result
  }
  const getShortWallet = (addr: any) => {
    if (addr) {
      if (addr === '0x0000000000000000000000000000000000000000') {
        return '-'
      }
      return `${addr.substring(0, 5)}...${addr.substring(addr.length - 4)}`
    }
    return '-'
  }

  const copyRefLink = () => {
    const address = `${window.location.origin}?ref=${account}`
    copyToClipboard(address)
    alert('Referral Link Copied')
  }

  return (
    <Div>
      <StyledReferal>
        <Heading size="lg">Get your friends join us finding MLX and earn commission!</Heading>
        <ul style={{ marginTop: '2rem' }}>
          <Text style={{ marginTop: '0.7rem' }}>
            {'\u25CF'}The reward will be sent automatically each time your referrals collect their MLX.
          </Text>
          {/* <Text style={{ marginTop: '0.7rem' }}>{'\u25CF'}Referrals do not lose 2% to his/her referrer</Text> */}
          <Text style={{ marginTop: '0.7rem' }}>
            {'\u25CF'}Get your friends to join us and earn commission in MLX !

          </Text>
          <Text style={{ marginTop: '0.7rem' }}>
            {'\u25CF'}Finally, you do not need to join us for receiving reward. But you are always welcomed!
          </Text>
          {/* <Row><Text>{'\u25CF'}</Text><Text>MustafaFinally, you do not need to join us for receiving reward. But you are always welcomed!</Text></Row> */}
        </ul>
        <Divider style={{ marginTop: '2rem' }} />

        <Row>
          <Padding>
            <CardValue2 fontSize="30px" value={refEarnings} decimals={0}  />
            <Text fontSize="16px">Earned Referral Commission</Text>
          </Padding>

          <Padding>
            <CardValue2 fontSize="30px" value={refCount} decimals={0} />
            <Text fontSize="16px">Total Referrals</Text>
          </Padding>

          <Padding>
            <Text fontSize="25px">{getShortWallet(myRef)}</Text>
            <Text fontSize="16px">Your Referee</Text>
          </Padding>
        </Row>

        {account ? (
          <Button
            fullWidth
            onClick={() => {
              copyRefLink()
            }}
          >
            Copy Your Referral Link
          </Button>
        ) : (
          <UnlockButton />
        )}
      </StyledReferal>
      {/* <Network /> */}
    </Div>
  )
}

export default Referal
